* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}
body {
  background-color: #8fbe00;
}
.App {
  width: 100%;
}
table {
  background-color: #f9f2e7;
  margin: 0 auto;
  margin-top: 50px;
  padding: 50px 40px;
  border: 1px solid rgb(199, 199, 199);
}
td {
  float: right;
  margin: 0 6px;
  width: 30px;
}
tr {
  position: relative;
  height: 30px;
  display: block;
  border: 1px solid rgb(199, 199, 199);
  font-size: 20px;
}
tr p {
  margin-left: 4px;
  position: absolute;
  /*
  -webkit-writing-mode: vertical-rl;
  -ms-writing-mode: vertical-rl;
  writing-mode: vertical-rl;*/
}
tr .kakkostyle {
  letter-spacing: -15px;
}

.bottom_wrapper {
  width: 95%;
  max-width: 890px;
  margin: 30px auto;
}
.bottom_wrapper textarea {
  background-color: #f9f2e7;
  width: 100%;
  height: 200px;
  padding: 10px;
  font-size: 20px;
}
.clear {
  clear: both;
}
